<template>
  <div class="content"  @click="contenClick">
    <!--     下拉框-->
    <Select2></Select2>
    <!--   统计表-->
    <div class="statis  statisTop">
      <div class="statiStical">
        <h2>
          <span></span><p>天天租订单数据 <img @click="labelClick" src="../../../assets/wh.png" alt=""></p>
          <div v-if="labelShow" class="labelText">指使用中、已完成租车订单</div>
        </h2>
        <ul>
          <li>
            <div>
              <span>支付订单数</span>
              <p>{{orderData.lPayCount}}</p>
            </div>
          </li>
          <li>
            <span>取车订单数</span>
            <p>{{orderData.lTakeCount}}</p>
          </li>
          <li>
            <span>退款订单数</span>
            <p>{{orderData.lRefundCount}}</p>
          </li>
        </ul>
      </div>
      <div class="marginTop">
        <!--        <eCharts></eCharts>-->
        <div id="eCharId2" class="eChars"></div>
        <div class="marginTop">
          <timeList  @timeChang="timeChang"></timeList>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  const timeList = () => import("../../../components/timeList3.vue");
  const Select2 = () => import("../../../components/Select2.vue");
  export default {
    components:{
      timeList,
      Select2
    },
    data() {
      return {
        labelShow:false,
        day:this.common.timeForMat(7,true)+" - "+this.common.timeForMat(0,true),
        orderData:{
          lPayCount:"",
          lRefundCount:"",
          lTakeCount:""
        },
        pathData:{
          beginDate:this.common.timeForMat(7,false),
          endDate:this.common.timeForMat(0,false),
          subAgentId:this.$store.state.subAgentId
        },
      }
    },
    computed:{
      changSelrct() {
        return this.$store.state.subAgentId
      }
    },
    watch:{
      changSelrct(val){
        this.pathData.subAgentId=val;
        this.orderAmount();
      }
    },
    mounted(){
      if(this.pathData.subAgentId!=""){
        this.orderAmount();
      }
    },
    methods:{
      labelClick(e){
        e.stopPropagation();
        if(this.labelShow){
          this.labelShow=false;
        }else {
          this.labelShow=true;

        }
      },
      contenClick(){
        this.labelShow=false;
      },
      orderAmount(){
        var that=this;
        this.http.get3("csh_m/statCenter/subAgentOrder/"+this.$store.state.pathCode,this.pathData,this.$store.state.token)
          .then(res => {
            if(res.data.code==600){
              console.log(res)
              if(res.data.result==null){
                this.common.eChart2("eCharId2",[],[],"");
                that.orderData.lPayCount=0;
                that.orderData.lRefundCount=0;
                that.orderData.lTakeCount=0;
              }else {
                let dataList=res.data.result.dataList;
                that.orderData.lPayCount=this.common.Money(res.data.result.lPayCount);
                that.orderData.lRefundCount=this.common.Money(res.data.result.lRefundCount);
                that.orderData.lTakeCount=this.common.Money(res.data.result.lTakeCount);
                let lPayArr=[];//支付
                let lRefundArr=[];//退款
                let lTakeArr=[];//取车
                let timesArr=[];
                dataList.forEach(function(item,index) {
                  lPayArr.push(item.lPayCount);
                  lRefundArr.push(item.lRefundCount);
                  lTakeArr.push(item.lTakeCount);
                  timesArr.push(item.statDate);
                });
                let datas=[timesArr,lPayArr,lRefundArr,lTakeArr];
                let nameArr=["支付订单","退款订单","取车订单"];
                this.common.eChart2("eCharId2",datas,nameArr,"");
              }
            }else{
              this.$dialog.alert({
                message:"订单数据失败！"
              }).then(() => {
              });
            }
          })
      },
      timeChang(data){
        if(data.length>10){
          this.pathData.beginDate=data.split(" - ")[0];
          this.pathData.endDate=data.split(" - ")[1];
        }else {
          this.pathData.beginDate=data;
          this.pathData.endDate=this.common.timeForMat(0,false);
        }
        console.log(data)
        this.orderAmount();
      },
      liClick(data,index){
        this.selectClick();
        this.selectName=data.name;
        this.itemId=index;
      }
    },
  }
</script>
<style scoped lang="scss">
  .eChars{
    width: 100%;
    height: 532px;
  }
  .marginTop{
    margin-top:20px;
  }
  .statis{
    background: #fff;
    padding-bottom: 42px;

  }
  .statisTop{
    margin-top:100px;
  }
  .WithdrawalData{
    border-top: 20px solid #F8F8F8;
    padding-bottom: 150px;
  }
  .statiStical{
    padding-top: 20px;
  }
  .statiStical h2{
    height: 60px;
    padding: 10px  36px;
    position: relative;
    .labelText{
      width:392px;
      height:80px;
      background:rgba(102,102,102,1);
      border-radius:8px;
      font-size:28px;
      font-family:PingFangSC;
      font-weight:400;
      color:rgba(255,255,255,1);
      line-height:80px ;
      position: absolute;
      left: 60px;
      top: 70px;
    }

  }
  .statiStical  h2 p{
    text-align: left;
    float: left;
    height: 40px;
    line-height: 40px;
    font-size:28px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    margin-left: 20px;
    position: relative;
    img{
      height:28px;
      width:28px;
      background-size:100% 100%;
      position: absolute;
      right: -38px;
      top: 6px;
    }
  }
  .statiStical  h2 span{
    height: 28px;
    width: 10px;
    background: #FF8D59;
    float: left;
    margin-top: 6px;
  }
  .statiStical ul{
    margin-top: 20px;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
  }
  .statiStical ul li{
    flex: 1;
    padding-left: 30px;
    box-sizing: border-box;
    border-left: 1px solid #E6E8EE;
  }
  .statiStical ul li:nth-child(1){
    border: none;
  }
  .statiStical ul li div{
    margin: 0 auto;
    display: inline-block;
  }
  .statiStical ul li span{
    font-size:24px;
    font-family:PingFangSC;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:34px;
    text-align: left;
    display: block;
  }
  .statiStical ul li p{
    text-align: left;
    font-size:48px;
    font-family:PingFangSC;
    font-weight:600;
    color:rgba(51,51,51,1);
    line-height:76px;
  }

  .content{
    min-height: 100%;
  }
  .selectList{
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    left: 0;
    top: 182px;
    background:rgba(0,0,0,0.5);
    height: 1140px;
    /*overflow: hidden;*/
    overflow: scroll;
    padding-bottom: 100px;
    width: 100%;
    z-index: 99999999;
    ul{
      padding: 0  40px;
      background: #ffffff;
      width: 100%;
      box-sizing: border-box;
      padding-bottom: 100px;
      li{
        padding:32px 0;
        width: 100%;
        box-sizing: border-box;
        font-size:26px;
        font-family:PingFangSC;
        font-weight:400;
        color:rgba(72,72,72,1);
        line-height:36px;
        text-align: left;
      }
      .actives{
        color: #FF8D59;
      }
    }
  }
  .Select{
    width: 100%;
    line-height: 96px;
    font-size:32px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    z-index: 999999999;
    left: 0;
    top: 86px;
    border-top:2px solid #F0F0F0 ;
    box-shadow:0px 2px 2px 0px  rgba(0,0,0,0.08);
    p{
      background: #ffffff;
      width: 100%;
      height:96px;
      text-align: left;
      padding: 0 100px 0 36px;
      box-sizing: border-box;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
    }
    .vantIcon{
      position: absolute;
      right: 36px;
      top: 4px;
      i{
        font-size: 32px;
        color: #333333;
        font-weight:600;
      }
    }
    .vantIcon.iconTop{
      transform: rotate(180deg) ;
      transition: transform 0.3s;
    }
    .vantIcon.iconbto{
      transform: rotate(0deg) ;
      transition: transform 0.3s;
    }
  }
</style>



